@font-face {
  font-family: "Roboto";
  src: local("Roboto"), local("Roboto-Regular"), url("../fonts/Robotoregular.woff2") format("woff2"), url("../fonts/Robotoregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto Bold"), local("Roboto-Bold"), url("../fonts/Robotobold.woff2") format("woff2"), url("../fonts/Robotobold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto-Light"), local("Roboto Light"), url("../fonts/Robotolight.woff2") format("woff2"), url("../fonts/Robotolight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Playfair Display";
  src: local("Playfair-Display"), local("Playfair Display"), url("../fonts/PlayfairDisplayregular.woff2") format("woff2"), url("../fonts/PlayfairDisplayregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Playfair Display";
  src: local("PlayfairDisplay-Bold"), local("PlayfairDisplay Bold"), url("../fonts/PlayfairDisplaybold.woff2") format("woff2"), url("../fonts/PlayfairDisplaybold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Prata";
  src: local("Prata"), local("Prata-Regular"), url("../fonts/Prataregular.woff2") format("woff2"), url("../fonts/Prataregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
::after,
::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  height: 100%;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: inline-block;
  vertical-align: top;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--default-white);
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-style: normal;
  min-height: 100%;
  color: var(--main-black);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

section {
  outline: 0.5px dotted #ffa200;
}

header {
  outline: 0.5px dotted red;
}

main {
  outline: 0.5px dotted green;
}

footer {
  outline: 0.5px dotted yellow;
}

:root {
  --default-black: #000000;
  --default-white: #ffffff;
  --arial: "Arial", "sans-serif", "Verdana";
  --tablet-width: 768px;
  --desktop-width: 1366px;
  --mobile-width: 320px;
  --main-font: "Roboto", "Arial", "sans-serif";
  --playfair-font: "Playfair Display", "Arial", "sans-serif";
  --main-black: #555248;
  --white-background1: #FDFDFB;
  --grey1: #E6E6E6;
  --yellow1: #E6C8B4;
  --black1: #1B1A17;
  --mobile-padding: 20px;
  --tablet-padding: 30px;
  --desktop-padding: 110px;
}

.main-header {
  background-color: var(--white-background1);
  line-height: 16px;
}

.main-header__wrapper {
  width: 100%;
  margin: 0 auto;
  padding-top: 34px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1440px) {
  .main-header__wrapper {
    max-width: var(--desktop-width);
  }
}

.main-header__logo-link {
  font-family: var(--playfair-font);
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.14em;
  display: flex;
  justify-content: flex-end;
  width: 107px;
  min-height: 26px;
  margin-bottom: 10px;
}

.main-header__logo-wrapper {
  align-self: center;
  width: 19.2px;
  height: 19.2px;
}

.main-header__logo-text {
  margin: 0;
  margin-top: 2px;
  margin-left: 3px;
}

.main-header__nav {
  width: 100%;
}

.main-header__nav-list {
  display: flex;
  flex-direction: column;
}

.main-header__nav-link {
  display: flex;
  justify-content: center;
  padding: 5px 0;
  position: relative;
}
.main-header__nav-link::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: var(--grey1);
  top: -1px;
  left: 0;
}
.main-header__nav-link::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: var(--grey1);
  bottom: 0;
  left: 0;
}

.main-header__utils {
  width: 100%;
}

.main-header__utils-list {
  display: flex;
  justify-content: space-around;
}

.main-header__utils-item {
  width: 100%;
}

.main-header__utils-link {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 10px 0;
}

.main-header__utils-link-icon {
  position: relative;
}

.main-header__utils-icon {
  position: relative;
}

.main-header__utils-round {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 13px;
  height: 13px;
  background: var(--yellow1);
  border-radius: 50%;
  top: -5px;
  right: -5px;
  font-size: 8px;
  color: var(--black1);
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 20px;
  line-height: 20px;
}

.main-utils__list {
  margin: 0;
  padding: 0;
  list-style: none;
}